//grid columns
@mixin make-column($columns, $grid: $grid-base) {
	box-sizing: border-box;
	width: percentage($columns / $grid); }

@mixin make-gutter($left-gutter: 0, $right-gutter: 0, $grid: $grid-base) {
	margin-left: percentage($left-gutter / $grid);
	margin-right: percentage($right-gutter / $grid); }

#demo-grid {
	.wrapper--flex {
		div {
			height: 150px;
			background: $grey;
			@include make-column(30 / 5);
			@include display-for(lg md) {
				@include make-column(10); }
			@include display-for(sm xs) {
				@include make-column(30); } }
		div:nth-of-type(2n + 2) {
			background: $black; } } }


