.wrapper {
	width: $wrapper-width;
	max-width: $wrapper-max-width;
	margin: 0 auto;
	&--flex {
		display: flex;
		&-wrap {
			flex-wrap: wrap; } }
	@include display-for(md sm xs) {
		width: 90%; } }
.wrapper--grey {
	background: $grey;
	max-width: 620px;
	margin: 0 auto;
	padding: 80px;
	@include display-for(md sm xs) {
		padding: 40px; } }
