.up-five-title {
	color: #1E46F4;
	text-align: center;
	margin: 0;
	transform: translateY(50%);
	font-size: 6rem;
	line-height: 7.2rem;
	font-weight: 400;
	@include display-for(md sm xs) {
		font-size: 4.4rem;
		line-height: 5.2rem; } }

.up-five-subtitle {
	font-size: 2rem;
	line-height: 3.5rem;
	width: 70%;
	text-align: center;
	margin: 30px auto 125px auto;
	line-height: 40px;
	color: #1E46F4;
	font-weight: 400;
	@include display-for(md sm xs) {
		margin: 30px auto; } }

.primary-title {
	background: #1E46F4;
	padding: 10px 55px 10px 55px;
	color: white;
	transform: translateX(-50%);
	display: inline-block;
	width: 108%;
	font-size: 2rem;
	line-height: 2.4rem;
	font-weight: normal;
	margin: 50px 0;
	@include display-for(md sm xs) {
		width: unset;
		transform: translate(-25%);
		white-space: nowrap; } }

.ua5-photo {
	position: absolute;
	top: 280px;
	right: 60px;
	max-width: 400px;
	opacity: 0.5;
	@include display-for(md sm xs) {
		display: none; } }

.up-five_image {
	max-width: 20%;
	display: block;
	@include display-for(md sm xs) {
		max-width: 30%; } }

.workshop-sponsor {
	margin-bottom: 40px; }
.workshop-sponsor_image {
	max-width: 70%;
	display: block;
	@include display-for(md sm xs) {
		max-width: 80%; } }

ul {
	list-style: none;
	padding-left: 0; }

p, li {
	font-size: 1.4rem;
	line-height: 1.9rem; }
p.schedule {
	margin: 5px 0; }
.tito-ticket-name, .tito-ticket-price {
	font-size: 2.2rem;
	line-height: 5rem; }
.tito-ticket-quantity {
	text-align: center; }
.tito-ticket-quantity input {
	line-height: 1.9rem;
	border: 2px solid black;
	padding: 5px 10px;
	margin-top: 20px;
	width: 95px; }
.tito-submit-wrapper {
	text-align: center;
	margin-bottom: 20px; }
.tito-submit {
	border: 2px solid black;
	padding: 10px 22px;
	font-size: 1.4rem;
	display: inline-block;
	text-decoration: none;
	color: black;
	background: $grey;
	transition: 0.3s; }

.tito-submit:hover {
	color: white;
	background: black; }

.affiliate_image {
	width: 160px;
	display: block;
	margin: 0 auto; }
.work-first {
		margin-bottom: 0.8rem; }
